import { useState, useEffect } from 'react';

const CardStackCollection = () => {
  const [isExpanded, setIsExpanded] = useState(false);
  const [hoveredCard, setHoveredCard] = useState(null);

  // Sample collection data - you can replace with your actual data
  const collections = [
    { id: 1, title: "Summer Vibes", image: "/api/placeholder/300/400", color: "bg-gradient-to-br from-orange-400 to-red-500" },
    { id: 2, title: "Urban Style", image: "/api/placeholder/300/400", color: "bg-gradient-to-br from-blue-400 to-indigo-600" },
    { id: 3, title: "Classic White", image: "/api/placeholder/300/400", color: "bg-gradient-to-br from-orange-500 to-pink-500" },
    { id: 4, title: "Street Wear", image: "/api/placeholder/300/400", color: "bg-gradient-to-br from-teal-400 to-cyan-600" },
    { id: 5, title: "Minimal", image: "/api/placeholder/300/400", color: "bg-gradient-to-br from-gray-400 to-gray-600" },
    { id: 6, title: "Bold Colors", image: "/api/placeholder/300/400", color: "bg-gradient-to-br from-yellow-400 to-orange-500" }
  ];

  const getCardStyle = (index, total) => {
    if (!isExpanded) {
      // Stacked style
      return {
        transform: `translateX(${index * 3}px) translateY(${index * -2}px) rotate(${(index - 2) * 1}deg)`,
        zIndex: total - index,
        transition: 'all 0.6s cubic-bezier(0.4, 0, 0.2, 1)'
      };
    } else {
      // Expanded fan style
      const angle = (index - (total - 1) / 2) * 8; // Spread angle
      const translateX = index * 180 - ((total - 1) * 180) / 2; // Horizontal spread
      const translateY = Math.abs(angle) * 2; // Slight arc effect
      
      return {
        transform: `translateX(${translateX}px) translateY(${translateY}px) rotate(${angle}deg)`,
        zIndex: hoveredCard === index ? 1000 : total - Math.abs(index - (total - 1) / 2),
        transition: 'all 0.8s cubic-bezier(0.34, 1.56, 0.64, 1)'
      };
    }
  };

  const handleCardHover = (index) => {
    if (isExpanded) {
      setHoveredCard(index);
    }
  };

  const handleCardLeave = () => {
    setHoveredCard(null);
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100 flex flex-col items-center justify-center p-8">
      {/* Header */}
      <div className="text-center mb-12">
        <h1 className="text-5xl font-bold text-gray-800 mb-4">
          The Ultimate
        </h1>
        <h2 className="text-6xl font-black text-gray-900 mb-8">
          COLLECTIONS
        </h2>
        
        {/* Decorative pills */}
        <div className="flex justify-center gap-4 mb-8">
          <span className="px-4 py-2 bg-cyan-400 text-white rounded-full text-sm font-medium">
            for everyone
          </span>
          <span className="px-4 py-2 bg-orange-400 text-white rounded-full text-sm font-medium">
            new styles
          </span>
        </div>
      </div>

      {/* Card Stack Container */}
      <div className="relative mb-12">
        <div className="relative w-80 h-96">
          {collections.map((collection, index) => (
            <div
              key={collection.id}
              className={`absolute w-full h-full rounded-2xl shadow-xl cursor-pointer overflow-hidden ${collection.color}`}
              style={getCardStyle(index, collections.length)}
              onMouseEnter={() => handleCardHover(index)}
              onMouseLeave={handleCardLeave}
              onClick={() => !isExpanded && setIsExpanded(true)}
            >
              {/* Card Content */}
              <div className="relative w-full h-full flex flex-col items-center justify-center p-6">
                {/* Placeholder for person image */}
                <div className="w-32 h-40 bg-white/20 rounded-xl mb-4 flex items-center justify-center">
                  <div className="w-16 h-20 bg-white/40 rounded-lg"></div>
                </div>
                
                {/* Collection title */}
                <h3 className="text-white font-bold text-lg text-center">
                  {collection.title}
                </h3>
                
                {/* Hover effect overlay */}
                {hoveredCard === index && (
                  <div className="absolute inset-0 bg-black/10 rounded-2xl transition-opacity duration-300"></div>
                )}
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Action Button */}
      <button
        onClick={() => setIsExpanded(!isExpanded)}
        className="px-8 py-3 bg-black text-white rounded-full font-medium hover:bg-gray-800 transition-colors duration-300 shadow-lg hover:shadow-xl"
      >
        {isExpanded ? 'Stack Cards' : 'Explore More'}
      </button>

      {/* Bottom text */}
      {isExpanded && (
        <p className="text-gray-600 text-center mt-8 max-w-md animate-fade-in">
          We Go First Choice Our Collections 12 Sessions We Create
          New Models Every Week. And We In A Few Items.
        </p>
      )}

      <style jsx>{`
        @keyframes fade-in {
          from { opacity: 0; transform: translateY(20px); }
          to { opacity: 1; transform: translateY(0); }
        }
        .animate-fade-in {
          animation: fade-in 0.6s ease-out;
        }
      `}</style>
    </div>
  );
};

export default CardStackCollection;